import Header from './components/Header';
import './styles/Home.css';
import { useState } from 'react';

function Home() {
    const [res, setRes] = useState(0);
    const btnPressed = () => {
        const url = document.querySelector("#url");
        if (url.value) {
            fetch("https://linkshort-backend.vercel.app/api/set", {
                method: 'POST',
                body: JSON.stringify({
                    "url": url.value
                }),
                headers: {
                    "content-type": "application/json"
                }
            })
                .then(res => res.json())
                .then(res => setRes(`https://linkshort.vercel.app/v/${res}`))
        }
        url.innerText = "";
    }

    const copyUrl = () => {
        document.querySelector("#copyurl").select();
        document.execCommand("copy");
        document.querySelector("#copybtn").innerText = "Copied";
    }

    return (
        <>
            <Header />
            <div class="container" style={{ paddingTop: '30vh' }}>
                {
                    res ?
                        <form method="POST" id="update_user">
                            <div class="new_user">
                                <div class="form-group">
                                    <input type="text" value={res} id="copyurl" autoComplete='off' />
                                </div>
                                <div class="form-group">
                                    <button type="button" onClick={copyUrl} id="copybtn" class="btn text-dark update">Copy URL</button>
                                </div>
                            </div>
                        </form>
                        :
                        <form method="POST" id="update_user">
                            <div class="new_user">
                                <div class="form-group">
                                    <input type="text" name="url" id="url" autoComplete='off' placeholder="https://linkshort.vercel.app" />
                                </div>
                                <div class="form-group">
                                    <button type="button" onClick={btnPressed} class="btn text-dark update">Add</button>
                                </div>
                            </div>
                        </form>
                }
            </div>
        </>
    );
}

export default Home;