import { useState, useEffect } from 'react';
import Header from './components/Header';
import './styles/Home.css';

function View(props) {
    const [url, setUrl] = useState(0);
    useEffect(() => {
        fetch(`https://linkshort-backend.vercel.app/api/find/${props.match.params.url}`)
            .then(res => res.json())
            .then(res => {
                if (res) {
                    setUrl(res.url)
                    window.location.replace(res.url)
                } else {
                    document.querySelector("#redirectBtn").innerText = "URL does not exits"
                    alert("URL does not exits");
                }
            })
    }, [])

    console.log(url);
    const BtnPressed = () => {
        fetch(`https://linkshort-backend.vercel.app/api/find/${props.match.params.url}`)
            .then(res => res.json())
            .then(res => {
                window.location.replace(res.url)
            })
    }

    return (
        <>
            <Header webname="Home" />

            <main id="site-main">
                <div class="container">
                    <div class="container">
                        <form method="POST" id="update_user">
                            <div class="new_user" style={{ paddingTop: '20vh' }}>
                                <div class="form-group">
                                    <button type="button" onClick={BtnPressed} id="redirectBtn" class="btn text-dark update">Click here if you not get redirect</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </main>
        </>
    );
}

export default View;